import React, { PureComponent } from "react";
import styles from "./ProductDetails.module.css";
import {
  getProductDetails,
  deleteProductDetails,
} from "../../Redux/actions/async";
import ImagesList from "../../Components/ImagesList/Images";
import ProductInfo from "../../Components/Product-Info/ProductInfo";
import Button from "../../Components/Button/Button";
import Parser from "html-react-parser";
import { connect } from "react-redux";
import { addToCart } from "../../Redux/actions/cart";
import { compose } from "redux";
import { withRouter } from "react-router";
import { uniqId } from "../../assets/utilize";
import { addQuantity, deleteAttributes } from "../../Redux/actions/cart";
import { addPrice } from "../../Redux/actions/cart";
export class ProductDetails extends PureComponent {
  state = {
    imageIndex: 0,
    selectedAttributes: [],
    message: "",
  };
  componentDidMount() {
    this.props.getProductDetails(this.props.match.params.id);
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevProps.symbol !== this.props.symbol) {
      const { prices } = this.props.PDPProduct;
      const { symbol } = this.props;
      const data = prices.find((item) => item.currency.symbol === symbol);
      this.setState({ price: data.amount });
    }
  }

  componentWillUnmount() {
    this.props.deleteAttributes();
    this.props.deleteProductDetails();
    caches.keys().then((names) => {
      names.forEach((name) => {
        caches.delete(name);
      });
    });
  }

  handleIndex(index) {
    this.setState({ imageIndex: index });
  }

  passProductToCart() {
    const { gallery, attributes, brand, name, prices, id, inStock } =
      this.props.PDPProduct;
    const { products, constAttributes, deleteAttributes } = this.props;
    const checkOptions = attributes.length === constAttributes.length;

    const order = {
      gallery,
      attributes,
      brand,
      name,
      prices,
      id,
      quantity: 1,
      selectedAttributes: constAttributes,
      uniqId: uniqId(),
    };
    const check = products.find(
      (item) =>
        item.id === order.id &&
        JSON.stringify(item.selectedAttributes) ===
          JSON.stringify(constAttributes)
    );
    if (inStock && checkOptions) {
      if (check) {
        const { uniqId, quantity } = check;
        this.props.addQuantity(uniqId, quantity + 1);
        this.setState({ message: "" });
        this.setState({ selectedAttributes: [] });
        deleteAttributes();
      } else {
        this.props.addToCart(order);
        this.setState({ message: "" });
        this.setState({ selectedAttributes: [] });
        deleteAttributes();
      }
    } else if (!inStock) {
      this.setState({ message: "This product out ot stock" });
    } else if (!checkOptions) {
      this.setState({ message: "you didn't select options" });
    }
  }
  handleGettingAttribute(attribute) {
    const newArr = [...this.state.selectedAttributes];
    const index = newArr.findIndex((item) => item.name === attribute.name);
    if (index === -1) {
      this.setState({
        selectedAttributes: [...this.state.selectedAttributes, attribute],
      });
    } else {
      newArr[index].value = attribute.value;
      this.setState({
        selectedAttributes: newArr,
      });
    }
  }

  render() {
    const {
      gallery,
      attributes,
      description,
      brand,
      name,
      inStock,
      prices,
      id,
    } = this.props.PDPProduct;
    const { overlayFlag, PDPProduct } = this.props;
    const { message } = this.state;
    return (
      <>
        <div
          className={overlayFlag ? styles.overlayLayout : styles.layout}
        ></div>
        {PDPProduct.gallery && (
          <div className={styles.container}>
            <div className={styles.imagesContainer}>
              <ImagesList gallery={gallery} inStock={inStock} />
            </div>

            <div className={styles.info}>
              {message && <p>{`we can't order because ${message}`}</p>}
              <ProductInfo
                attributes={attributes}
                brand={brand}
                name={name}
                prices={prices}
                price={this.state.price}
                inStock={inStock}
                cartPage={true}
                id={id}
                getAttribute={(option) => this.handleGettingAttribute(option)}
              />

              <Button
                handleClicked={() => this.passProductToCart()}
                text={inStock ? "order now" : "Out of stock"}
                btnClass={styles.orderBtn}
              />
              <div className={styles.description}>{Parser(description)}</div>
            </div>
          </div>
        )}
      </>
    );
  }
}
const mapStateToProps = (state) => ({
  symbol: state.symbol,
  cart: state.cart,
  overlayFlag: state.cart.overlayFlag,
  products: state.cart.cartProducts,
  constAttributes: state.cart.constAttributes,
  PDPProduct: state.products.PDPProduct,
});
const mapDispatchToProps = () => ({
  addToCart,
  addQuantity,
  deleteAttributes,
  getProductDetails,
  deleteProductDetails,
  addPrice,
});
export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps())
)(ProductDetails);
